@keyframes fade-in {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.teamwork-makes-the {
  margin: 0;
  position: relative;
  font-size: inherit;
  font-weight: 600;
  font-family: inherit;
  display: inline-block;
  width: 636px;
  flex-shrink: 0;
}
.d-ellpse-icon,
.main-bot-icon {
  position: absolute;
  top: 411px;
  left: 0;
  width: 630px;
  height: 166px;
}
.main-bot-icon {
  width: 67.36%;
  top: 0;
  right: 14.23%;
  left: 18.41%;
  max-width: 100%;
  overflow: hidden;
  height: 552px;
  object-fit: cover;
  opacity: 0;
}
.main-bot-icon.animate {
  animation: 3s ease 0s 1 normal forwards fade-in;
}
.hero {
  position: relative;
  width: 630px;
  height: 577px;
  flex-shrink: 0;
}
.hero-section {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  padding: var(--padding-4xs) var(--padding-2xl);
  align-items: center;
  justify-content: center;
  gap: var(--gap-sm);
}
.about-img-icon {
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  height: 507px;
  object-fit: cover;
}
.about-us {
  position: relative;
  font-weight: 600;
}
.one-of-the-container {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-lg);
}
.about-us-parent {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-sm);
}
.about {
  align-self: stretch;
  background-color: var(--color-gray-100);
  display: flex;
  flex-direction: row;
  padding: var(--padding-sm) 73px;
  align-items: center;
  justify-content: center;
  gap: 76px;
  text-align: left;
  font-size: var(--font-size-3xl);
  color: var(--color-gray-200);
  font-family: var(--font-poppins);
}
.competitions-we-compete {
  position: relative;
  font-weight: 600;
}
.competitons,
.home {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  color: var(--color-gray-200);
  font-family: var(--font-poppins);
}
.competitons {
  align-self: stretch;
  overflow: hidden;
  padding: var(--padding-sm) 80px;
  align-items: center;
  gap: 35px;
  text-align: center;
  font-size: var(--font-size-5xl);
}
.home {
  position: relative;
  background-color: var(--color-gray-100);
  width: 100%;
  overflow-x: auto;
  align-items: flex-start;
  gap: var(--gap-xl);
  text-align: left;
  font-size: 64px;
}
@media screen and (max-width: 960px) {
  .about-img-icon {
    flex: unset;
    align-self: stretch;
  }
  .about-us {
    font-size: 50px;
  }
  .about-us,
  .one-of-the-container {
    text-align: justify;
  }
  .about-us-parent {
    flex: unset;
    align-self: stretch;
  }
  .about {
    flex-direction: column;
  }
}
